"use client";

import { useState } from "react";
import { QuizPhase } from "@/components/quiz-phase";
import { RoulettePhase } from "@/components/roulette-phase";
import { Loader2 } from "lucide-react";

type Phase = "quiz" | "loading" | "roulette";

export default function TVQuizApp() {
  const [phase, setPhase] = useState<Phase>("quiz");

  const handleQuizComplete = () => {
    setPhase("loading");
    // Simula um processamento (ou espera o Supabase depois)
    setTimeout(() => {
      setPhase("roulette");
    }, 2000);
  };

  const handleReset = () => {
    setPhase("quiz");
  };

  return (
    // Fundo refinado: um laranja sólido vibrante com um gradiente radial sutil no centro
    <div className="min-h-screen bg-[radial-gradient(ellipse_at_center,_var(--tw-gradient-stops))] from-[#ff8533] to-[#FF6B00] overflow-hidden selection:bg-white selection:text-[#FF6B00]">
      {phase === "quiz" && <QuizPhase onComplete={handleQuizComplete} />}

      {phase === "loading" && (
        <div className="flex min-h-screen flex-col items-center justify-center animate-in fade-in zoom-in duration-500">
          <div className="bg-white/20 p-8 rounded-full backdrop-blur-md animate-spin mb-8">
            <Loader2 className="w-24 h-24 text-white" />
          </div>
          <h2 className="text-4xl font-bold text-white uppercase tracking-widest animate-pulse">
            Processando Respostas...
          </h2>
        </div>
      )}

      {phase === "roulette" && <RoulettePhase onReset={handleReset} />}
    </div>
  );
}
